.box-main {
  text-align: center;
  margin: 50px;
}

.link-to {
  text-decoration: none;
  color: #000;
}

.box-panduan {
  margin: 0 auto;
  margin-top: 50px;
  width: 80%;
  height: 350px;
  border-radius: 20px;
}

.box-panduan:hover {
  box-shadow: 10px 10px 20px #383838;
  transition: 0.5s;
}

.panduanImage {
  width: 100%;
  height: 70%;
  object-fit: cover;
  object-position: center;
  border-radius: 20px 20px 0 0;
}

.text-panduan {
  background-color: #ffcdcd;
  padding: 20px;
  border-radius: 0 0 20px 20px;
}
